/*==============================================================
# Typography: General typography styles
==============================================================*/

html {
}
body {
	word-wrap: break-word;
	color: $brand-charcoal;
	font: $font-light-weight 16px / 1.5 $sansSerif;
	letter-spacing: $kerning50;
}
body,
button,
input,
select,
textarea {
	font-size: 16px;
	font-size: 1rem;
	line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
	text-rendering: optimizeLegibility;
	margin-top: 0;
	font-weight: 300;
    letter-spacing: 1px;
    line-height: 1.5;
    .sans-serif {
    	font-family: $sansSerif;
    }
}
h1 {
  font-size: 2.44140625rem;
  margin-bottom: 1.75rem;
  font-family: $serif;
}
h2 {
  font-size: 2.074em;
  margin-bottom: 1.61616rem;
  font-family: $serif;
}
h3 {
  font-size: 1.728em;
  margin-bottom: 1.21212rem;
  font-family: $serif;
}
h4 {
  font-size: 1.44em;
  margin-bottom: 1rem;
  font-family: $serif;
}
h5 {
  font-size: 1.2em;
  margin-bottom: 0.80808rem;
}
h6 {
  font-size: 1em;
  margin-bottom: 0.60606rem;
}
p {
	line-height: 1.5;
    margin: 0 0 1em;
    hyphens: auto; 
}
pre {
	background: #eee;
	font-family: "Courier 10 Pitch", Courier, monospace;
	font-size: 15px;
	font-size: 0.9375rem;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}
code,
kbd,
tt,
var {
	font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 15px;
	font-size: 0.9375rem;
}
abbr,
acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}
mark,
ins {
	background: #fff9c0;
	text-decoration: none;
}
big {
	font-size: 125%;
}
small {
  font-size: 65%;
}
.sans-serif {
	font-family: $sansSerif;
}
.serif {
	font-family: $serif;
}