/*==============================================================
# Media
==============================================================*/

figure {
	max-width: 100%;
}

.media-container {
	img {
		width: 100%;
	}
}
.section-featured-video {
	.component-content {
		position: relative;
		float: left;
		@include breakFirst(medium) {
			width: 100%;
			height: 0;
			padding-bottom: 60%;
		}
		.featured-video-details {
			width: 100%;
			position: relative;
			background: $grayDark;
			padding: 25px 30px;
			color: $gray;
		 	@include breakFirst(medium) {
		 		position: absolute;
                padding: 60px 45px 60px;
                width: 50%;
                &.top-left,
				&.top-right {
					top: -50px;
				}
				&.top-right,
				&.bottom-right {
					right: 0;
				}
				&.top-left,
				&.bottom-left {
					left: 0;
				}
				&.bottom-left,
				&.bottom-right {
					bottom: -50px;
				}
            }
		}
		.media-label {
			font-size: .75em;
			text-transform: uppercase;
		}
        .modal-trigger a {
            width: 40px;
            height: 40px;
            display: inline-block;
            margin: 20px 0 0;
            svg {
            	position: relative;
    			left: 2px;
            }
        }
        .icon {
        	padding: 10px;
        	border: 1px solid $white;
        	border-radius: 50%;
        }
        .icon,
        svg {
            display: inline-block;
            width: 100%;
            height: 100%;
            fill: $white;
        }
	}
}

/* Images */

.background-thumbnail {
	@include bdg-cover;
}
figcaption {
	font-size: .875em;
	line-height: .7em;
	border-left: 5px solid $grayDark;
	padding: 0px 15px;
	margin: 15px 0;
	p {
			margin: 0;
	}
	a {
			line-height: 1.5;
	}
}
.post-overview {
	figure:first-child {
		margin: 0 0 1em;
	}
	.media-container {
		@include bdg-cover;
		height: 0;
		width: 100%;
		padding-bottom: 60%;
	}
}
.mosaic-ratio {
	height: 0;
	width: 100%;
	padding-bottom: 60%;
	position: relative;
	@include breakFirst(medium) {
		top: 0px;
		margin-top:  0px;
		padding-bottom: 110%;
	}
	@include breakFirst(large) {
		padding-bottom: 90%;
	}
	@include breakFirst(wide) {
		padding-bottom: 70%;
	}
}
.circle-thumbnail {
	@extend .mosaic-ratio;
	@include breakFirst(medium) {
			padding-bottom: 0%;
			overflow: hidden;
			float: left;
			position: relative;
			width: 150px;
			height: 150px;
			left: 45px;
			top: 80px;
			margin-top: -80px;
			z-index: 2;
			border-radius: 100%;
	}
}
.flex-background {
	height: 0;
	width: 100%;
	padding-bottom: 60%;
	position: relative;
	@include breakFirst(medium) {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		z-index: 0;
		padding: 0;
	}
}
/* General Embed */

.embed-object {
	> * {
		display: block !important;
		margin-left: auto;
    	margin-right: auto;
	}
}

/* Audio */

audio {
	width: 100%;
}
.track-info {
	display: table;
	width: 100%;
	border-top: 2px solid $grayDark;
	border-right: 2px solid $grayDark;
	border-left: 2px solid $grayDark;
	h5 {
		margin: 0;
		font-size: 1.35em;
	}
	.track-artist,
	.track-album {
		font-weight: bold;
		text-transform: uppercase;
		font-size: .65em;
	}
	.track-album {
		color: $grayDark;
	}
}
.track-thumbnail {
	@include bdg-cover;
	height: 95px;
	width: 95px;
	display: table-cell;
	vertical-align: middle;
	@include breakFirst(medium) {
		height: 150px;
		width: 150px;
	}
	img {
		width: 100%;
		float: left;
	}
}
.track-details {
	display: table-cell;
	vertical-align: middle;
	padding: 15px;
}
.track-controls {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: flex-start;
	background: $white;
	border: 2px solid $grayDark;
	padding: 5px;
	.track-play-pause,
	.track-volume {
		width: 32px;
		height: 32px;
		flex: 0 0 auto;
		position: relative;
		.icon {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}
	.current-time,
	.total-time {
		font-size: 14px;
		display: flex;
		flex: 0 0 auto;
		min-width: 45px;
		color: $grayDark;
		span {
			display: block;
			text-align: left;
    		margin-left: 10px;
		}
	}
	.track-progress {	
		display: flex;
		min-width: 25px;
		flex: 1 1 auto;
		height: 20px;
		padding: 0 10px;
	}
	.range-slider {
		width: 100%;
	}
}
.play-pause-icon {
	box-sizing: border-box;
	width: 20px;
	height: 20px;
	border-width: 10px 0 10px 15px;
	display: inline-block;
	border-color: transparent transparent transparent $grayDark;
	transition: .1s all ease;
	will-change: border-width;
	cursor: pointer;
	border-style: solid;
	.playing & {
		border-style: double;
		border-width: 0px 0 0px 15px;
	}
}
.volume-icon {
	height: 100%;
	width: 100%;
	color: $grayDark;
	svg {
		height: 100%;
		width: 100%;
		padding: 7px;
	}
	g {
		transition: .4s ease all;
	}
}
.muted {
	.volumn-icon {
		.speaker {
	    	position: relative;
    		transform: translateX(5px);;
		}
		.sound {
			opacity: 0;
		}
	}
}
.track-volume {
	position: relative;
	&:hover {
		.range-slider {
			width: 100px;
		}
	}
	.range-slider {
		position: absolute;
		top: 0;
		left: 0;
		overflow: hidden;
		transition: .4s width ease;
		height: 16px;
		width: 0px;
		position: absolute;
		z-index: 5;
		transform: translateX(0%) rotate(270deg);
		transform-origin: 6px 0px;
		border-radius: 20px;
	}
	input[type=range] {
		writing-mode: bt-lr;
		-webkit-appearance: none;
		-webkit-tap-highlight-color: rgba(255, 255, 255, 0);
		box-sizing: border-box;
		border: none;
		width: 100px;
		height: 16px;
		padding: 0;
		margin: 0 auto;
		display: block;
		background-image: linear-gradient($grayDark 0%, $grayDark 100%);
		background-size: 100% 100%;
		background-repeat: no-repeat;
		outline: none; /* no focus outline */
		border: 2px solid $grayDark;
		border-radius: 20px;
		&::-webkit-slider-runnable-track {
			box-sizing: border-box;
			border: none;
			width: 100%;
			height: 100%;
			background: transparent;
		}
		&::-moz-range-track {
			border: inherit;
			height: 100%;
			width: 100%;
			background: transparent;
		}
		&::-ms-track {
			border: inherit;
			height: 100%;
			width: 100%;
			color: transparent; /* don't drawn vertical reference line */
			background: transparent;
		}
		&::-ms-fill-lower,
		&::-ms-fill-upper {
			background: transparent;
		}
		&::-ms-tooltip {
			display: none;
		}
		&::-webkit-slider-thumb {
			-webkit-appearance: none;
			opacity: 0;
			box-sizing: border-box;
			height: 20px;
			width: 20px;
		}
		&::-moz-range-thumb {
			opacity: 0;
			box-sizing: border-box;
			height: 20px;
			width: 20px;
		}
		&::-ms-thumb {
			opacity: 0;
			box-sizing: border-box;
			height: 20px;
			width: 20px;
		}
	}
	.volumn-button {
		width: 100%;
		height: 100%;
	}
}
.track-progress {
	.range-slider {
		overflow: hidden;
	}
	input[type="range"] {
		-webkit-appearance: none;
		-webkit-tap-highlight-color: rgba(255, 255, 255, 0);
		box-sizing: border-box;
		width: 100%;
		height: 100%;
		margin: 0;
		border: none;
		padding: 0;
		background-image: linear-gradient($grayDark 0%, $grayDark 100%);
		background-size: 0% 100%;
		background-repeat: no-repeat;
		outline: none; /* no focus outline */
		box-shadow: inset 0 0 0 2px $grayDark;
		border-radius: 20px;
		float: left;
		&::-webkit-slider-runnable-track {
			box-sizing: border-box;
			border: none;
			width: 100%;
			height: 100%;
			background: transparent;
		}
		&::-moz-range-track {
			border: inherit;
			height: 100%;
			background: transparent;
		}
		&::-ms-track {
			border: inherit;
			height: 100%;
			color: transparent; /* don't drawn vertical reference line */
			background: transparent;
		}
		&::-ms-fill-lower,
		&::-ms-fill-upper {
			background: transparent;
		}
		&::-ms-tooltip {
			display: none;
		}
		&::-webkit-slider-thumb {
			-webkit-appearance: none;
			opacity: 0;
			box-sizing: border-box;
			height: 20px;
			width: 20px;
		}
		&::-moz-range-thumb {
			opacity: 0;
			box-sizing: border-box;
			height: 20px;
			width: 20px;
		}
		&::-ms-thumb {
			opacity: 0;
			box-sizing: border-box;
			height: 20px;
			width: 20px;
		}
	}
}
.responsive-audio {
	iframe {
		width: 100%;
	}
}

.responsive-embed {
	img {
		width: 100%;
	}
}

/* Videos */

video {
	width: 100%;
}

.responsive-video {
		position: relative;
		padding-bottom: 56.25%; /* 16:9 */
		height: 0;
		iframe {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
		}
}

/* Plyr.js css */

@keyframes plyr-progress {
    to {
        background-position: 25px 0
    }
}

@keyframes plyr-popup {
    0% {
        opacity: .5;
        transform: translateY(10px)
    }
    to {
        opacity: 1;
        transform: translateY(0)
    }
}

@keyframes plyr-fade-in {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }
}

.plyr {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    direction: ltr;
    font-family: Avenir, "Avenir Next", "Helvetica Neue", "Segoe UI", Helvetica, Arial, sans-serif;
    font-variant-numeric: tabular-nums;
    font-weight: 500;
    line-height: 1.7;
    max-width: 100%;
    min-width: 200px;
    position: relative;
    text-shadow: none;
    transition: box-shadow .3s ease
}

.plyr audio,
.plyr video {
    border-radius: inherit;
    height: auto;
    vertical-align: middle;
    width: 100%
}

.plyr button {
    font: inherit;
    line-height: inherit;
    color: $white;
}

.plyr:focus {
    outline: 0
}

.plyr--full-ui {
    box-sizing: border-box
}

.plyr--full-ui *,
.plyr--full-ui::after,
.plyr--full-ui::before {
    box-sizing: inherit
}

.plyr--full-ui a,
.plyr--full-ui button,
.plyr--full-ui input,
.plyr--full-ui label {
    touch-action: manipulation
}

.plyr__badge {
    background: #4f5b5f;
    border-radius: 2px;
    color: #fff;
    font-size: 9px;
    line-height: 1;
    padding: 3px 4px
}

.plyr--full-ui::-webkit-media-text-track-container {
    display: none
}

.plyr__captions {
    animation: plyr-fade-in .3s ease;
    bottom: 0;
    color: #fff;
    display: none;
    font-size: 14px;
    left: 0;
    padding: 10px;
    position: absolute;
    text-align: center;
    transform: translateY(-40px);
    transition: transform .4s ease-in-out;
    width: 100%
}

.plyr__captions span {
    background: rgba(0, 0, 0, .8);
    border-radius: 2px;
    -webkit-box-decoration-break: clone;
    box-decoration-break: clone;
    line-height: 185%;
    padding: .2em .5em;
    white-space: pre-wrap
}

.plyr__captions span div {
    display: inline
}

.plyr__captions span:empty {
    display: none
}

@media (min-width:480px) {
    .plyr__captions {
        font-size: 16px;
        padding: 20px
    }
}

@media (min-width:768px) {
    .plyr__captions {
        font-size: 18px
    }
}

.plyr--captions-active .plyr__captions {
    display: block
}

.plyr--hide-controls .plyr__captions {
    transform: translateY(-15px)
}

.plyr__control {
    background: 0 0;
    border: 0;
    border-radius: 3px;
    color: inherit;
    cursor: pointer;
    flex-shrink: 0;
    overflow: visible;
    padding: 7px;
    position: relative;
    transition: all .3s ease
}

.plyr__control svg {
    display: block;
    fill: currentColor;
    height: 18px;
    pointer-events: none;
    width: 18px
}

.plyr__control:focus {
    outline: 0
}

.plyr__control.plyr__tab-focus {
    box-shadow: 0 0 0 3px rgba($grayDark, .35);
    outline: 0
}

.plyr__control[aria-pressed=false] .icon--pressed,
.plyr__control[aria-pressed=false] .label--pressed,
.plyr__control[aria-pressed=true] .icon--not-pressed,
.plyr__control[aria-pressed=true] .label--not-pressed {
    display: none
}

.plyr--audio .plyr__control.plyr__tab-focus,
.plyr--audio .plyr__control:hover,
.plyr--audio .plyr__control[aria-expanded=true] {
    background: $grayDark;
    color: #fff
}

.plyr__control--overlaid {
    background: rgba($grayDark, 1);
    border: 0;
    border-radius: 100%;
    box-shadow: 0 1px 1px rgba(0, 0, 0, .15);
    color: #fff;
    display: none;
    left: 50%;
    padding: 15px;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 2
}

.plyr__control--overlaid svg {
    height: 50px;
    width: 50px;
    padding: 10px;
    left: 2px;
    position: relative;
}

.plyr__control--overlaid:focus,
.plyr__control--overlaid:hover {
    background: lighten($grayDark, 20%);
    fill: $white;
    color: #fff
}

.plyr--playing .plyr__control--overlaid {
    opacity: 0;
    visibility: hidden
}

.plyr--full-ui.plyr--video .plyr__control--overlaid {
    display: block
}

.plyr--full-ui::-webkit-media-controls {
    display: none
}

.plyr__controls {
    align-items: center;
    display: flex;
    text-align: center
}

.plyr__controls .plyr__menu,
.plyr__controls .plyr__progress,
.plyr__controls .plyr__time,
.plyr__controls>.plyr__control {
    margin-left: 5px
}

.plyr__controls .plyr__menu:first-child,
.plyr__controls .plyr__menu:first-child+[data-plyr=pause],
.plyr__controls .plyr__progress:first-child,
.plyr__controls .plyr__progress:first-child+[data-plyr=pause],
.plyr__controls .plyr__time:first-child,
.plyr__controls .plyr__time:first-child+[data-plyr=pause],
.plyr__controls>.plyr__control:first-child,
.plyr__controls>.plyr__control:first-child+[data-plyr=pause] {
    margin-left: 0
}

.plyr__controls .plyr__volume {
    margin-left: 5px
}

@media (min-width:480px) {
    .plyr__controls .plyr__menu,
    .plyr__controls .plyr__progress,
    .plyr__controls .plyr__time,
    .plyr__controls>.plyr__control {
        margin-left: 10px
    }
    .plyr__controls .plyr__menu+.plyr__control,
    .plyr__controls>.plyr__control+.plyr__control,
    .plyr__controls>.plyr__control+.plyr__menu {
        margin-left: 5px
    }
}

.plyr--video .plyr__controls {
    background: linear-gradient(transparent, rgba(0, 0, 0, .7));
    border-bottom-left-radius: inherit;
    border-bottom-right-radius: inherit;
    bottom: 0;
    color: #fff;
    left: 0;
    padding: 35px 10px 10px;
    position: absolute;
    right: 0;
    transition: opacity .4s ease-in-out, transform .4s ease-in-out;
    z-index: 2
}

.plyr--video .plyr__controls .plyr__control svg {
    -webkit-filter: drop-shadow(0 1px 1px rgba(0, 0, 0, .15));
    filter: drop-shadow(0 1px 1px rgba(0, 0, 0, .15))
}

.plyr--video .plyr__controls .plyr__control.plyr__tab-focus,
.plyr--video .plyr__controls .plyr__control:hover,
.plyr--video .plyr__controls .plyr__control[aria-expanded=true] {
    background: $grayDark;
    color: #fff
}

.plyr--audio .plyr__controls {
    background: #fff;
    border-radius: inherit;
    color: #4f5b5f;
    padding: 10px
}

.plyr--video.plyr--hide-controls .plyr__controls {
    opacity: 0;
    pointer-events: none;
    transform: translateY(100%)
}

.plyr [data-plyr=airplay],
.plyr [data-plyr=captions],
.plyr [data-plyr=fullscreen],
.plyr [data-plyr=pip] {
    display: none
}

.plyr--airplay-supported [data-plyr=airplay],
.plyr--captions-enabled [data-plyr=captions],
.plyr--fullscreen-enabled [data-plyr=fullscreen],
.plyr--pip-supported [data-plyr=pip] {
    display: inline-block
}

.plyr__video-embed {
    height: 0;
    padding-bottom: 56.25%;
    position: relative
}

.plyr__video-embed iframe {
    border: 0;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    width: 100%
}

.plyr--full-ui .plyr__video-embed iframe {
    pointer-events: none
}

.plyr--full-ui .plyr__video-embed>div {
    padding-bottom: 240%;
    position: relative;
    transform: translateY(-38.28125%)
}

.plyr__menu {
    display: flex;
    position: relative
}

.plyr__menu .plyr__control svg {
    transition: transform .3s ease
}

.plyr__menu .plyr__control[aria-expanded=true] svg {
    transform: rotate(90deg)
}

.plyr__menu .plyr__control[aria-expanded=true] .plyr__tooltip {
    display: none
}

.plyr__menu__container {
    animation: plyr-popup .2s ease;
    background: rgba(255, 255, 255, .9);
    border-radius: 4px;
    bottom: 100%;
    box-shadow: 0 1px 2px rgba(0, 0, 0, .15);
    color: #4f5b5f;
    font-size: 16px;
    margin-bottom: 10px;
    position: absolute;
    right: -3px;
    text-align: left;
    white-space: nowrap;
    z-index: 3
}

.plyr__menu__container>div {
    overflow: hidden;
    transition: height .35s cubic-bezier(.4, 0, .2, 1), width .35s cubic-bezier(.4, 0, .2, 1)
}

.plyr__menu__container::after {
    border: 4px solid transparent;
    border-top-color: rgba(255, 255, 255, .9);
    content: '';
    height: 0;
    position: absolute;
    right: 15px;
    top: 100%;
    width: 0
}

.plyr__menu__container ul {
    list-style: none;
    margin: 0;
    overflow: hidden;
    padding: 7px
}

.plyr__menu__container ul li {
    margin-top: 2px
}

.plyr__menu__container ul li:first-child {
    margin-top: 0
}

.plyr__menu__container .plyr__control {
    align-items: center;
    color: #4f5b5f;
    display: flex;
    font-size: 14px;
    padding: 4px 14px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    width: 100%
}

.plyr__menu__container .plyr__control::after {
    border: 4px solid transparent;
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%)
}

.plyr__menu__container .plyr__control--forward {
    padding-right: 28px
}

.plyr__menu__container .plyr__control--forward::after {
    border-left-color: rgba(79, 91, 95, .8);
    right: 5px
}

.plyr__menu__container .plyr__control--forward.plyr__tab-focus::after,
.plyr__menu__container .plyr__control--forward:hover::after {
    border-left-color: currentColor
}

.plyr__menu__container .plyr__control--back {
    font-weight: 500;
    margin: 7px;
    margin-bottom: 3px;
    padding-left: 28px;
    position: relative;
    width: calc(100% - 14px)
}

.plyr__menu__container .plyr__control--back::after {
    border-right-color: rgba(79, 91, 95, .8);
    left: 7px
}

.plyr__menu__container .plyr__control--back::before {
    background: #b7c5cd;
    box-shadow: 0 1px 0 #fff;
    content: '';
    height: 1px;
    left: 0;
    margin-top: 4px;
    overflow: hidden;
    position: absolute;
    right: 0;
    top: 100%
}

.plyr__menu__container .plyr__control--back.plyr__tab-focus::after,
.plyr__menu__container .plyr__control--back:hover::after {
    border-right-color: currentColor
}

.plyr__menu__container label.plyr__control {
    padding-left: 7px
}

.plyr__menu__container label.plyr__control input[type=radio]+span {
    background: rgba(0, 0, 0, .1);
    border-radius: 100%;
    display: block;
    flex-shrink: 0;
    height: 16px;
    margin-right: 10px;
    position: relative;
    transition: all .3s ease;
